@import url("https://fonts.googleapis.com/css2?family=Parisienne&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
* {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 1.3rem;
}

::-webkit-scrollbar-track {
  background-color: #1f3048;
}
::-webkit-scrollbar-thumb {
  background-color: #ec3a58;
  border-radius: 1rem;
  border: 0.5rem solid transparent;
  background-clip: content-box;
}

.navbarWrapper {
  width: 100%;
  height: auto;
  position: fixed;
  display: flex;
  justify-content: space-between;
  background-color: #162447;
  color: #ffffff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  z-index: 99;
}

.logo {
  font-size: 40px;
  font-family: "Parisienne", cursive;
  color: #ffffff;
}
.logo:hover {
  color: #ec3a58;
}

.navLeft {
  /* border: 1px solid blue; */
  margin-left: 30px;
}

.navRight {
  display: flex;
  /* border: 1px solid black; */
  align-items: center;
  margin-right: 30px;
}

.navLeftLink {
  text-decoration: none !important;
}

.navRight > a {
  text-decoration: none;
  outline: none;
  padding: 10px;
  font-size: 18px;
  font-family: "Poppins", sans-serif;
}
.navRight > a:hover {
  cursor: pointer;
  color: #ec3a58;
  text-decoration: underline;
  text-underline-offset: 4px;
}

.navRight > a:active,
a:link,
a:visited,
a:hover {
  color: #ffffff;
}

.navRightSmall {
  display: flex;
  align-items: center;
  margin-right: 20px;
}

.navRightSmall .MenuItem {
  background-color: #1f3048;
  font-family: "Poppins";
  color: #ffffff;
}

.navRightSmall .MenuItem:hover {
  color: #ec3a58 !important;
}

.navRightSmall .MenuButton {
  font-size: 22px;
}
.navRightSmall .MenuButton:hover {
  background-color: #1f3048;
}

.homeWrapper {
  text-align: center;
  height: 60vh;
  width: 100%;
  background-image: linear-gradient(to bottom right, #16244799, #1f406899),
    url("./brandBanner.png");
  /* border: 2px solid red; */
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 70px;
}
.child {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
}
.typed {
  color: #ffffff;
  /* padding-top: 100px; */
  margin: auto;
  font-size: 50px;
  font-family: "Poppins";
  margin-top: 30px;
}

.typedDesc {
  color: #ffffff;
  /* display: flex; */
  padding-top: 30px;
  /* padding-left: 150px; */
  font-size: 32px;
  font-family: "Poppins";
}

.resumeButton {
  padding-top: 30px;
  /* padding-right: 150px; */
  display: flex;
  align-items: center;
  margin: auto;
  justify-content: center;
  margin-bottom: 30px;
}
.resumeButton > a {
  outline: none;
  border: none;
  font-size: 24px;
  color: #ffffff;
  display: flex;
  align-items: center;
  /* justify-content: center; */
}
.resumeButton > a:hover {
  color: #ec3a58;
  text-decoration: underline;
  text-underline-offset: 4px;
  cursor: pointer;
}

.aboutWrapper {
  width: 100%;
  background-color: #162447;
}

.aboutBox {
  width: 90%;
  margin: auto;
  background-color: #162447;
  display: flex;
  justify-content: space-between;
  padding-top: 50px;
  padding-bottom: 50px;
  /* border: 1px solid orangered; */
}

.aboutBox > .aboutLeft {
  width: 40%;
  /* border: 1px solid red; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.aboutLeft > img {
  /* border-radius: 30%; */
  width: 50%;
  /* height: 150px; */
}

.aboutBox > .aboutRight {
  /* border: 1px solid blue; */
  width: 55%;
  display: flex;
  align-items: center;
}

.about {
  margin-left: 20px;
  color: #ffffff !important;
  font-size: 24px;
  display: inline;
  font-family: "Poppins";
  border-bottom: 4px solid white;
}

/* .name {
  margin-top: 20px;
  font-size: 30px;
  font-family: "Poppins";
  color: #ffffff !important;
  margin-left: 20px;
} */

.aboutdesc {
  margin-left: 20px;
  font-family: "Poppins";
  color: #ffffff !important;
  margin-top: 20px;
}

.projectWrapper {
  width: 100%;
  background-color: #1f3048;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  padding-top: 50px;
  padding-bottom: 50px;
}

.projectWrapper > p {
  font-size: 24px;
  border-bottom: 4px solid ivory;
  display: inline;
  color: #ffffff;
  margin-bottom: 50px;
}

.projects {
  width: 97%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.projects > div {
  height: auto;
  /* border: 2px solid blue; */
  padding-bottom: 10px;
  border-radius: 10px;
  /* box-shadow: rgba(253, 147, 1,0.9) 0px 5px 15px; */
  box-shadow: rgb(105, 108, 5) 0px 3px 8px;
}
.projects img {
  height: auto;
  width: 100%;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}
.techStack {
  display: flex;
  align-items: center;
}
.techStack > p {
  font-weight: "bold";
  font-size: 18px;
}
