.project2,
.project3,
.project4,
.project5,
.project6 {
} */

.skillsWrapper {
  width: 100%;
  background-color: #162447;
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.toolsWrapper {
  width: 100%;
  background-color: #162447;
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skillsWrapper > p {
  text-align: center;
  font-size: 24px;
  font-family: "Poppins";
  display: inline;
  color: #ffffff;
  margin-bottom: 50px;
  border-bottom: 4px solid ivory;
}

.toolsWrapper > p {
  text-align: center;
  font-size: 24px;
  font-family: "Poppins";
  display: inline;
  color: #ffffff;
  margin-bottom: 50px;
  border-bottom: 4px solid ivory;
}

.techStacksBox {
  width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 50px;
  row-gap: 30px;
}

.techStacks {
  border-radius: 10px;
}

.techStacks > img {
  width: 65px;
  margin-bottom: 10px;
  transition: all 0.3s ease-in-out;
}

.techStacks > img:hover {
  transform: scale(1.1);
}

#upx {
  transition: all 0.8s ease-in-out;
}
#upx:hover {
  transform: scale(1.1);
}

