.gitCalender {
  width: 100%;
  background-color: #1f3048;
  display: flex;
  padding: 50px 0;
  flex-direction: column;
  align-items: center;
}

.gitCalender > .calenders {
  color: #ffffff;
  font-size: 24px;
  font-family: "Poppins";
  border-bottom: 4px solid ivory;
  margin-bottom: 50px;
}

.gitCalender > .stats {
  color: #ffffff;
  font-size: 24px;
  font-family: "Poppins";
  border-bottom: 4px solid ivory;
  margin-top: 50px;
}

.contactWrapper {
  width: 100%;
  background-color: #162447;
  padding: 50px 0;
}

.contactBox {
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: space-between;
}

.contactLeft {
  width: 48%;
  display: flex;
  justify-content: center;
  /* border: 1px solid red; */
}

.contactLeft > img {
  width: 100%;
}

.contactRight {
  width: 48%;
  /* border: 1px solid blue; */
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}
.contactRight > p {
  color: #ffffff;
  font-size: 24px;
  display: inline;
  font-family: "Poppins";
  border-bottom: 4px solid #ffffff;
}

.contactRight > span {
  color: #ffffff;
  font-size: 18px;
  font-family: "Poppins";
  margin-top: 0px;
  margin-bottom: 10px;
  text-align: center;
}

.num,
.email {
  text-align: center;
  color: #ffffff;
  margin-bottom: 10px;
}

.icons {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  /* padding: 40px; */
  margin-bottom: 20px;
}

@media all and (min-width: 450px) and (max-width: 819px) {
  .aboutBox {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .aboutBox > .aboutLeft {
    width: 60%;
    margin-left: 20px;
  }

  .aboutBox > .aboutRight {
    width: 85%;
  }
  .aboutLeft > img {
    width: 70%;
  }
}

@media all and (min-width: 30px) and (max-width: 449px) {
  .aboutBox {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .aboutBox > .aboutLeft {
    width: 60%;
    margin-bottom: 20px;
  }

  .aboutBox > .aboutRight {
    width: 95%;
  }
  .aboutLeft > img {
    width: 100%;
  }

  .about {
    font-size: 20px;
  }
  .name {
    font-size: 24px;
  }
}

